*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  display: flex;
  min-height: 100%;
  flex-direction: column;
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  width: 430px;
  font-family: "Space Grotesk", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: black;
  font-weight: 400;
}
@media (min-width: 1440px) {
  .container {
    width: 1440px;
    font-size: 18px;
    line-height: 27px;
    color: black;
    font-weight: 400;
  }
}

.page-main {
  padding: 0 20px;
  overflow: hidden;
}
@media (min-width: 1440px) {
  .page-main {
    padding: 0 100px;
  }
}

@font-face {
  font-family: "Space Grotesk";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Space Grotesk"), url("https://mtrenkina.github.io/pozitivus-landing/fonts/SpaceGrotesk-Regular.woff2") format("woff2"), url("https://mtrenkina.github.io/pozitivus-landing/fonts/SpaceGrotesk-Regular.woff") format("woff");
}
@font-face {
  font-family: "Space Grotesk";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Space Grotesk"), url("https://mtrenkina.github.io/pozitivus-landing/fonts/SpaceGrotesk-Medium.woff2") format("woff2"), url("https://mtrenkina.github.io/pozitivus-landing/fonts/SpaceGrotesk-Medium.woff") format("woff");
}
.button {
  width: 100%;
  height: 68px;
  color: white;
  background-color: black;
  border-radius: 14px;
  cursor: pointer;
  font-size: 20px;
  line-height: 28px;
}
.button--green {
  background-color: #B9FF66;
  color: black;
}
.button:hover {
  opacity: 0.8;
}

.main-heading {
  font-size: 36px;
  line-height: 54px;
  color: black;
  font-weight: 400;
  background-color: #B9FF66;
  width: fit-content;
  padding: 0 10px;
  margin: 0 auto;
  margin-bottom: 30px;
  border-radius: 7px;
  font-weight: 500;
}

.main-description {
  padding-bottom: 40px;
  text-align: left;
}

.page-header {
  padding: 30px 0;
  position: relative;
}
@media (min-width: 1440px) {
  .page-header {
    padding: 60px 0 70px 0;
  }
}

.main-nav {
  position: relative;
  padding: 0 20px;
}
@media (min-width: 1440px) {
  .main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: auto;
    padding: 0 100px;
  }
}
.main-nav__logo img {
  width: 144px;
  height: 23.61px;
}
@media (min-width: 1440px) {
  .main-nav__logo img {
    width: 219.54px;
    height: 36px;
  }
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__wrapper {
  position: absolute;
  background: linear-gradient(white, #F3F3F3);
  border-radius: 14px;
  padding: 20px 40px;
  right: 0px;
  width: 100%;
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    position: static;
    width: auto;
    background: none;
    padding-left: 0;
    padding-right: 0;
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  gap: 15px;
}
@media (min-width: 1440px) {
  .main-nav__list {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;
  }
}
.main-nav__toogle {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  top: 4px;
  right: 30px;
  z-index: 2;
  border: 0;
  cursor: pointer;
  color: black;
  background-color: transparent;
}
@media (min-width: 1440px) {
  .main-nav__toogle {
    display: none;
  }
}
.main-nav--opened .main-nav__toogle::before, .main-nav--opened .main-nav__toogle::after {
  content: "";
  position: absolute;
  top: 10px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: currentColor;
}
.main-nav--opened .main-nav__toogle::before {
  transform: translateY(-50%) rotate(45deg);
}
.main-nav--opened .main-nav__toogle::after {
  transform: translateY(-50%) rotate(-45deg);
}
.main-nav--closed .main-nav__toogle::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: black;
  box-shadow: 0 8px 0 0 #000, 0 16px 0 0 #000;
}
@media (max-width: 1439px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    display: block;
    position: relative;
  }
  .main-nav--nojs .main-nav__toogle {
    display: none;
  }
}

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1439px) {
  .main-nav:not(.main-nav--nojs) .site-list__item:not(.site-list__item--link) {
    animation: ani 0.5s ease-out;
  }
}
@keyframes ani {
  0% {
    transform: translateX(-20%);
    opacity: 0;
  }
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}
