@keyframes animMarquee {
  0% {
    transform: translateX(300%);
  }
  100% {
    transform: translateX(-330%);
  }
}
@keyframes animLeftMarquee {
  0% {
    transform: translateX(-330%);
  }
  100% {
    transform: translateX(300%);
  }
}
