.site-list {
  @include mxn-list-style;

  @media (max-width: $mobile-width-max) {
    .main-nav:not(.main-nav--nojs) &__item:not(.site-list__item--link) {
      animation: ani 0.5s ease-out;
    }
  }

  @keyframes ani {
    0% {
      transform: translateX(-20%);
      opacity: 0;
    }
    100% {
      transform: translateX(0%);
      opacity: 1;
    }
  }

  &__item a {
    @include mxn-link-style;
    padding: 5px;

    @media (min-width: $desktop-width) {
      @include mxn-font-style(20px, 28px);
    }
  }

  &__item a:hover {
    opacity: 0.6;
  }

  &__item--link {
    padding: 20px 35px;
    border: 1px solid black;
    border-radius: 14px;
  }
}
